import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private _registerUrl = 'https://mialdtodoapp.herokuapp.com/register';
  private _loginUrl = 'https://mialdtodoapp.herokuapp.com/login';
 private  _taskUrl  = 'https://mialdtodoapp.herokuapp.com/todoitem';
  constructor(private http: HttpClient, private _router: Router) { }

  registerUser(user){
   return this.http.post<any>(this._registerUrl, user)
  }
  loginUser(user){
    return this.http.post<any>(this._loginUrl, user)
  }
  loggedIn(){
    return !!localStorage.getItem('token');
  }
  logoutUser(){
    localStorage.removeItem('token');
    this._router.navigate(['/home'])
  }
  getToken(){
    return localStorage.getItem('token')
  }
  addTask(task){
    return  this.http.post<any>(this._taskUrl,task);
  }
  getTasks(){
    return  this.http.get<any>(this._taskUrl);
  }
  removeTask(itemId){
    const options = {
      headers: new HttpHeaders({
        'Content-Type':'application/json'
      }),
      body:{
        itemId: itemId
      }
    }
    return  this.http.delete<any>(this._taskUrl,options);
  }
}

